body{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}
.home{
    display: grid;
    justify-self:center
}
.home h2{
    text-align: center;
}
.lmo{
    background-color: rgb(24, 6, 100);
    margin:1.5rem;
    padding:1rem;
    border-radius: 0.5rem ;
    display: grid;
    grid-gap:10px;
    grid-template-columns: 1fr 1fr;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   color: azure;
    }
.ige{
border-radius:10px;

    }
    .navigation {
        margin-top: 10px;
        margin-left: 1rem;
        margin-right: 1rem;
        border-radius:5px;
        background-color:  rgb(24, 6, 100);
        height: 50px;
        display: flex;
      
        justify-content: space-around;
    }
    .navigation a{
        
        
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
       text-decoration: none;
        list-style:none;
        justify-content:space-around;
        color: white;
    }
    .navigation a:hover{
        color: aquamarine;
        cursor: pointer;
        
    }
    .countrieslist{
       
        justify-content: center;
    }
    
    .countrieslist ul{
     display: flex;
     flex-wrap: wrap;
    }
    .inputul{
        display: flex;
        justify-content:center;
        background-color:rgb(24, 6, 100); 
        margin: 1rem;
        height:3rem;
        list-style: none;
        color: white;
    }
    img{
        height:10rem; 
        width:100%;
        background-size: cover;
    }
    .cards{
        text-align:center ;
        font-size:0.5rem;
        margin: 0.3rem;
        display:grid;
        width: 12rem;
        height:16.8rem;
        background-color:rgb(24, 6, 100); 
        border: white solid 2px;
        border-radius: 3px ;
        justify-content: center;
        color:wheat
        
    }
    .writter{
        margin-top: 2rem;
        display:grid;
        height: 500px;
        width: 500px;
        background:rgb(24, 6, 100); 
        justify-self: center;
        border-radius: 10px;
        position: relative;
        
    }
    .writter input{
        margin:40px;
        padding-left:20px;
       border-radius: 5px;
        height:40px; 
        outline: none;
    }
    .writter textarea{
        position: absolute;
        margin:40px;
        top:10%;
        padding-left:20px;
        border-radius: 5px;
        height: 150px;
        width:400px;
       outline: none;
       font-family: Arial, Helvetica, sans-serif;
        
    }
    .articles{
       display:grid;
       justify-content: center;
       justify-items: center;
        
    }
    .article{
        position: relative;
        margin: 1rem;
        background-color: rgb(144, 140, 197);
        width:700px;
        border-radius: 8px;
        
    }
    .article p{
        padding: 1rem;
        
    }
    .article button{
        margin: 1rem;
        float:right;
        border-radius:4px;
        background: green;
        
    }
    .article h2{
       
        margin: 1rem;
        top:0;
        left:0;
    }
    .article #date{
        position: absolute;
        margin: 1rem;
        top:0;
        right:0;
        color: black;
        
        
        
    }
    #edit{
        margin-left:50px;
        width:600px;
        height: 120px;
        border-radius: 8px;
        outline: none;
        font-family: Arial, Helvetica, sans-serif;
         
    }